<template>
	<view class="home">
		<view class="home-header">
			<view :style="{height:statusbarHeight +'px'}"></view>
			<view class="home-header_content" :style="{height:navbarHeight+'px',width:windowWith+'px'}">
				<view class="home-header_box">
					<view class="header-icon">
						<uni-icons type="search" size="16" color="#999"></uni-icons>
					</view>
					<view class="header-search">请输入内容</view>
				</view>

			</view>
		</view>
		<view style="height: 45px;"></view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				statusbarHeight: 0,
				navbarHeight: 45,
				windowWith: 375
			};
		},
		created() {
			const info = uni.getSystemInfoSync()
			this.statusbarHeight = info.statusBarHeight
			this.windowWith = info.windowWidth
			console.log(info)

			//#ifndef H5 || APP-PLUS || MP-ALIPAY
			const menuButtonInfo = uni.getMenuButtonBoundingClientRect()
			// this.navbarHeight = menuButtonInfo.height
			this.navbarHeight = (menuButtonInfo.bottom - info.statusbarHeight) + (menuButtonInfo.top - info.statusbarHeight)
			// console.log('menuButtonInfo',menuButtonInfo)
			this.windowWith = menuButtonInfo.left
			//#endif
		}
	}
</script>

<style lang="scss">
	.home {
		display: flex;
		flex-direction: column;

		.home-header {
			display: flex;
			align-items: center;
			justify-content: center;
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			z-index: 2;
			background-color: $mk-base-color;
			
			.home-header_content {
				display: flex;
				align-items: center;
				justify-content: center;
				height: 45px;
				box-sizing: border-box;
				.home-header_box {

					display: flex;
					align-items: center;
					width: 315px;
					background-color: #fff;
					height: 34px;
					border-radius: 30px;
					font-size: 14px;
					color: #999;

					.header-icon {
						padding:0 6px;
					}
				}
			}



		}
	}
</style>
